               '.flex-offset-#{$suffix}, ' +\n                '.layout-margin .flex-offset-#{$suffix}, ' +\n                '.layout-margin .offset-#{$suffix}';\n        }\n\n        #{$offsets} {\n            margin-left: calc(100% / 3);\n        }\n    }\n\n    @each $i in 66 {\n        $offsets : '';\n        $suffix : '';\n\n        @each $s in $sizes {\n            @if $s != '' {  $suffix : '#{$s}-#{$i}';   }\n            @else        {  $suffix : '#{$i}';         }\n\n            $offsets :\n                '.offset-#{$suffix}, ' +\n                '.flex-offset-#{$suffix}, ' +\n                '.layout-margin .flex-offset-#{$suffix}, ' +\n                '.layout-margin .offset-#{$suffix}';\n        }\n\n        #{$offsets} {\n            @include rtl-prop(margin-left, margin-right, calc(200% / 3), auto);\n        }\n    }\n}\n\n@mixin layout-for-name($name: null) {\n    @if $name == null { $name : '';          }\n    @if $name != ''   { $name : '-#{$name}'; }\n\n    .layout#{$name},\n    .layout#{$name}-column,\n    .layout#{$name}-row {\n        box-sizing: border-box;\n        display: -webkit-box;\n        display: -webkit-flex;\n        display: -moz-box;\n        display: -ms-flexbox;\n        display: flex;\n\n        min-width: 0;\n        min-height: 0;\n    }\n\n    .layout#{$name}-column {\n        flex-direction: column;\n    }\n\n    .layout#{$name}-row {\n        flex-direction: row;\n    }\n}\n\n@mixin flex-properties-for-name($name: null) {\n    $flexName: 'flex';\n    @if $name != null {\n        $flexName: 'flex-#{$name}';\n        $name: '-#{$name}';\n    } @else {\n        $name: '';\n    }\n\n    // === flex: 1 1 0%;\n    .#{$flexName}             { flex: 1;         box-sizing: border-box; }\n\n    .#{$flexName}-grow        { flex: 1 1 100%;  box-sizing: border-box; }\n\n    .#{$flexName}-initial     { flex: 0 1 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-auto        { flex: 1 1 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-none        { flex: 0 0 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-noshrink    { flex: 1 0 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-nogrow      { flex: 0 1 auto;  box-sizing: border-box; }\n\n    // (1-20) * 5 = 0-100%\n    @for $i from 0 through 20 {\n        $value : #{$i * 5 + '%'};\n\n        .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: #{$value};\n            max-height: 100%;\n            box-sizing: border-box;\n        }\n\n        .layout-row > .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: #{$value};\n            max-height: 100%;\n            box-sizing: border-box;\n\n            // Required by Chrome M48+ due to http://crbug.com/546034\n            @if $i == 0 {  min-width: 0;  }\n        }\n\n        .layout-column > .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: 100%;\n            max-height: #{$value};\n            box-sizing: border-box;\n        }\n\n        .layout#{$name}-row > .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: #{$value};\n            max-height: 100%;\n            box-sizing: border-box;\n\n            // Required by Chrome M48+ due to http://crbug.com/546034\n            @if $i == 0 {  min-width: 0;  }\n        }\n\n        .layout#{$name}-column > .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: 100%;\n            max-height: #{$value};\n            box-sizing: border-box;\n\n            // Required by Chrome M48+ due to http://crbug.com/546034\n            @if $i == 0 {  min-height: 0;  }\n        }\n    }\n\n    @if ($name == '') {\n        .flex-33 { flex: 1 1 100%;  max-width: 33.33%;  max-height: 100%; box-sizing: border-box; }\n\n        .flex-66 { flex: 1 1 100%;  max-width: 66.66%;  max-height: 100%; box-sizing: border-box; }\n    }\n\n    .layout-row {\n        > .#{$flexName}-33 { flex: 1 1 33.33%;  max-width: 33.33%;  max-height: 100%; box-sizing: border-box; }\n\n        > .#{$flexName}-66 { flex: 1 1 66.66%;  max-width: 66.66%;  max-height: 100%; box-sizing: border-box; }\n    }\n\n    .layout-column {\n        > .#{$flexName}-33 { flex: 1 1 33.33%;  max-width: 100%;  max-height: 33.33%; box-sizing: border-box; }\n\n        > .#{$flexName}-66 { flex: 1 1 66.66%;  max-width: 100%;  max-height: 66.66%; box-sizing: border-box; }\n    }\n\n    .layout#{$name}-row {\n        > .#{$flexName}-33 { flex: 1 1 100%;  max-width: 33.33%;  max-height: 100%; box-sizing: border-box; }\n\n        > .#{$flexName}-66 { flex: 1 1 100%;  max-width: 66.66%;  max-height: 100%; box-sizing: border-box; }\n\n        // Required by Chrome M48+ due to http://crbug.com/546034\n        > .flex { min-width: 0;  }\n    }\n\n    .layout#{$name}-column {\n        > .#{$flexName}-33 { flex: 1 1 100%;  max-width: 100%;  max-height: 33.33%; box-sizing: border-box; }\n\n        > .#{$flexName}-66 { flex: 1 1 100%;  max-width: 100%;  max-height: 66.66%; box-sizing: border-box; }\n\n        // Required by Chrome M48+ due to http://crbug.com/546034\n        > .flex { min-height: 0; }\n    }\n}\n\n@mixin layout-align-for-name($suffix: null) {\n\n    // Alignment attributes for layout containers' children\n    // Arrange on the Main Axis\n    // center, start, end, space-between, space-around\n    // flex-start is the default for justify-content\n    // ------------------------------\n\n    $name: 'layout-align';\n    @if $suffix != null {\n        $name: 'layout-align-#{$suffix}';\n    }\n\n    .#{$name},\n    .#{$name}-start-stretch // defaults\n    {\n        justify-content : flex-start;\n        align-content : stretch;\n        align-items: stretch;\n    }\n\n    // Main Axis Center\n    .#{$name}-start,\n    .#{$name}-start-start,\n    .#{$name}-start-center,\n    .#{$name}-start-end,\n    .#{$name}-start-stretch\n    {\n        justify-content: flex-start;\n    }\n\n    // Main Axis Center\n    .#{$name}-center,           //stretch\n    .#{$name}-center-start,\n    .#{$name}-center-center,\n    .#{$name}-center-end,\n    .#{$name}-center-stretch\n    {\n        justify-content: center;\n    }\n\n    // Main Axis End\n    .#{$name}-end, //stretch\n    .#{$name}-end-start,\n    .#{$name}-end-center,\n    .#{$name}-end-end,\n    .#{$name}-end-stretch\n    {\n        justify-content: flex-end;\n    }\n\n    // Main Axis Space Around\n    .#{$name}-space-around, //stretch\n    .#{$name}-space-around-center,\n    .#{$name}-space-around-start,\n    .#{$name}-space-around-end,\n    .#{$name}-space-around-stretch\n    {\n        justify-content: space-around;\n    }\n\n    // Main Axis Space Between\n    .#{$name}-space-between, //stretch\n    .#{$name}-space-between-center,\n    .#{$name}-space-between-start,\n    .#{$name}-space-between-end,\n    .#{$name}-space-between-stretch\n    {\n        justify-content: space-between;\n    }\n\n\n    // Arrange on the Cross Axis\n    // center, start, end\n    // stretch is the default for align-items\n    // ------------------------------\n\n    // Cross Axis Start\n    .#{$name}-start-start,\n    .#{$name}-center-start,\n    .#{$name}-end-start,\n    .#{$name}-space-between-start,\n    .#{$name}-space-around-start\n    {\n        align-items: flex-start;\n        align-content: flex-start;\n    }\n\n    // Cross Axis Center\n    .#{$name}-start-center,\n    .#{$name}-center-center,\n    .#{$name}-end-center,\n    .#{$name}-space-between-center,\n    .#{$name}-space-around-center\n    {\n        align-items: center;\n        align-content: center;\n        max-width: 100%;\n    }\n\n    // Cross Axis Center IE overflow fix\n    .#{$name}-start-center > *,\n    .#{$name}-center-center > *,\n    .#{$name}-end-center > *,\n    .#{$name}-space-between-center > *,\n    .#{$name}-space-around-center > *\n    {\n        max-width: 100%;\n        box-sizing: border-box;\n    }\n\n    // Cross Axis End\n    .#{$name}-start-end,\n    .#{$name}-center-end,\n    .#{