left: 0;\n        }\n    }\n\n    &__ol {\n        margin: 0;\n        padding-left: 32px;\n\n        & .docs-markdown__li {\n            padding-left: 4px;\n        }\n\n        & & {\n            list-style-type: lower-alpha;\n            padding-left: 16px;\n        }\n    }\n\n    &__table {\n        text-align: left;\n        padding-top: 16px;\n    }\n\n    &__th, &__td {\n        padding: 8px;\n        vertical-align: top;\n\n        &:first-child {\n            padding-left: 16px;\n        }\n\n        &:last-child {\n            padding-right: 16px;\n        }\n    }\n\n    &__code {\n        white-space: pre-wrap;\n    }\n\n    &__pre>&__code {\n        display: block;\n        position: relative;\n        overflow-x: auto;\n        padding: 12px;\n    }\n\n    &-code-block {\n        display: flex;\n        position: absolute;\n        top: 0;\n        right: 0;\n        z-index: 10;\n        padding: 6px 6px;\n\n        &__copied {\n            display: none;\n            font-size: 14px;\n        }\n\n        &_success {\n            .docs-markdown-code-block__copied {\n                display: block;\n            }\n\n            .docs-markdown__code-icon {\n                display: none;\n            }\n        }\n    }\n}\n\n.docs-header-link {\n    position: relative;\n\n    & span {\n        position: absolute;\n        display: block;\n        left: 0;\n        top: -60px;\n    }\n}\n\n@mixin docs-markdown-theme($theme) {\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $foreground: map-get($theme, foreground);\n    $is-dark: map-get($theme, is-dark);\n\n    $text: mc-color($foreground, text);\n    $color: mc-color($primary);\n    $color_hover: mc-color($primary, if($is-dark, lighter, darker));\n    $color_disabled: mc-color($foreground, text-disabled);\n    $fg-default: mc-color($foreground, text);\n    $th-color: mc-color($second, 400);\n    $divider: mc-color($foreground, divider);\n\n    .docs-header-link {\